.about {
  max-width: 1180px;
  margin: 0 auto;

  &__container {
    padding: 50px 20px;
    @include grid;
    grid-column: 1 / - 1;

    @include onTablet {
      grid-column: 1 / -1;
      margin: 65px 27px;
    }

    @include onDesktop {
      margin: 100px 50px;
    }
  }

  &__content {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 1 / 4;
    }

    @include onDesktop {
      grid-column: 2 / 6;
    }
  }

  &__title {
    grid-column: 1 / -1;
  }

  &__our-brand {
    margin-bottom: 30px;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;

  }

  &__ingred {
    padding-bottom: 10px;
  }

  &__part2 {
    display: none;

    @include onDesktop {
      display: block;
      margin-bottom: 20px;
    }
  }

  &__link {

    padding-bottom: 10px;
    width: fit-content;
    text-transform: uppercase;
    color: $coloBbgMenu;
    text-decoration: none;

    @include onDesktop {
      position: relative;
      padding-bottom: 10px;
      width: fit-content;
      text-transform: uppercase;
      color: $coloBbgMenu;
      text-decoration: none;
      @include lineAfterHover;
    }
  }

  &__photo {

    @include onTablet {
      grid-column: 4 / -1;
    }

    @include onDesktop {
      grid-column: 7 / -1;
      display: flex;
      gap: 20px;
    }
  }

  &__slide-img {
    display: none;
    @include hover(transform, scale($scale));

    @include onTablet {
      display: block;
      object-fit: cover;
      transition: $effectDuration;

    }
  }

  &__img-half {
    display: none;
    @include hover(transform, scale($scale));

    @include onDesktop {
      display: flex;
      margin-top: 100px;
      width: 180px;
      height: 430px;

      object-fit: cover;
      transition: $effectDuration;
    }
  }
}

.vertical {
  display: none;

  @include onDesktop {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    gap: 10px;
  }

  &__text {
    display: flex;
    justify-content: center;
    align-items: center;
    writing-mode: vertical-lr;
    transform: rotate(180deg);
    color: rgba(52, 79, 16, 0.5);
  }
}
